                              <p>Тяжелые задачи для эрудитов</p>
                              <button @click="start(2)">Начать на сложной</button>
                            </div>
                          </div>
                        </div>
                      </div>
                      </div>
                </div>
            </template>
            <template v-else-if="isTask == true">
                <div id="test">
                    <template v-if="counter < 10">
                        <h2>
                          {{taskInfo}}<template v-if="counter !== 0 && difficulty !== 2">?</template>
                        </h2>
                        <testComponent :tasks="tasks" @req-sub="respond"/>
                    </template>
                    <template v-else-if="counter == 10">
                        <h1>
                            {{result}}
                        </h1>
                        <h2>
                            {{comment}}
                        </h2>
                        <template v-if="trueCounter !== 10">
                            <ol>   
                                <li v-for="(data, index) in shown" :key="index">
                                    {{ data }}
                                </li>
                            </ol>
                        </template>
                        <button @click="reset">На главную</button>
                        <button @click="reDo">переделать</button>
                    </template>
                </div>
            </template>
            <div class="foot">
                <hr>
                <p id="footer">
                    Этот сайт является проектом учеников школы 1538. Он использует фреймворк Vue, а также материалы с codepen.
                </p>
            </div>
</template>

<script>
let data = [
    [
        [{text: 'из бетона', stat: true}, {text: 'из фарфора', stat: true}, {text: 'из осмия', stat: false}, {text: 'из железа', stat: false}],
        [{text: '1200Н', stat: false}, {text: '1275Н', stat: true}, {text: '4800Н', stat: false}, {text: '1000Н', stat: false}],
        [{text: '50кПа', stat: false}, {text: '25кПа', stat: false}, {text: '100кПа', stat: false}, {text: '36кПа', stat: true}],
        [{text: 'P=pgh (р - "ро")', stat: true}, {text: 'P=pVg (р - "ро")', stat: false}, {text: 'q=ghV', stat: false}, {text: 'q=Smg', stat: false}],
        [{text: '15кН', stat: false}, {text: '5Н', stat: false}, {text: '8кН', stat: true}, {text: '1.25Н', stat: false}],
        [{text: '11627Па', stat: false}, {text: '100кПа', stat: false}, {text: '179Па', stat: false}, {text: '7467Па', stat: true}],
        [{text: 'парафин', stat: false}, {text: 'сухая сосна', stat: true}, {text: 'стекло', stat: false}, {text: 'сталь', stat: false}],
        [{text: '2.5кПа', stat: true}, {text: '10кПа', stat: false}, {text: '100Па', stat: false}, {text: '900Па', stat: false}],
        [{text: '647263кДж', stat: false}, {text: '1мДж', stat: false}, {text: '50000кДж', stat: false}, {text: '43200кДж', stat: true}],
        [{text: 'нет', stat: true}, {text: 'да', stat: false}],
    ],
    [
        [{text: 'для увеличения сцепления с дорогой зимой', stat: true}, {text: 'для красоты', stat: false}, {text: 'для того, чтобы показать производителя', stat: false}, {text: 'для большей прочности', stat: false}],
        [{text: '15м', stat: false}, {text: '5м', stat: false}, {text: '50м', stat: false}, {text: '107,5м', stat: true}],
        [{text: 'увеличиться', stat: false}, {text: 'ни на сколько', stat: true}, {text: 'уменьшится', stat: false}],
        [{text: '25кДж', stat: false}, {text: '50кДж', stat: false}, {text: '16 кДж', stat: true}, {text: '100кДж', stat: false}],
        [{text: 'уменьшится', stat: true}, {text: 'увеличиться', stat: false}, {text: 'не измениться', stat: false}],
        [{text: '36кг', stat: false}, {text: '100 кг', stat: false}, {text: '25кг', stat: false}, {text: '56.25кг', stat: true}],
        [{text: 'это вес груза на борту', stat: false}, {text: 'это грузоподъемность судна', stat: false}, {text: 'это общий вес объема воды, которую вытесняет судно ', stat:  true}, {text: 'это вода, находящяяся в судне', stat: false}],
        [{text: '6500 Н', stat: true}, {text: '59000 Н', stat: false}, {text: '10000 Н', stat: false}, {text: '100000000 Н', stat: true}],
        [{text: '10А', stat: true}, {text: '100А', stat: false}, {text: '225А', stat: false}, {text: '0.1А', stat: false}],
        [{text: 'Ф-изм. магн. потока', stat: true}, {text: 'увеличение напряжения', stat: false}, {text: 'уменьшение силы тока', stat: false}, {text: 'увеличение индуктивности', stat: false}],
    ],
    [
        [{text: 'силы тока', stat: false}, {text: 'длины', stat: true}, {text: 'амплитуды', stat: false}, {text: 'ни от чего больше не зависит', stat: false}],
        [{text: 'частота кадров монитора', stat: true}, {text: 'сила тока в устройстве', stat: false}, {text: 'времени (милисекунды) на обработку информации', stat: false}, {text: 'кол-во ядер в процессоре', stat: false}],
        [{text: 'задний фасад здания театра', stat: false}, {text: 'название древнегреческого представления', stat: false}, {text: 'название самой последней симфонии Баха', stat:  true}, {text: 'элементарная частица', stat: false}],
        [{text: 'NaCl', stat: true}, {text: 'Бр', stat: false}, {text: 'SiCa', stat: false}, {text: 'ClKu2S', stat: false}],
        [{text: 'величина i (как число)', stat: false}, {text: 'формула работы', stat: false}, {text: 'формула магн.поля', stat: false}, {text: 'формула самоиндукции', stat: true}],
        [{text: 'формула, по которой считается прочность аллюминия', stat: false}, {text: 'сила электрона', stat: false}, {text: 'формула энергии заряда в эл.поле', stat:  true}, {text: 'масса нейтрино', stat: false}],
        [{text: 'разряд протекающий за счет внешнего ионизатора', stat: true}, {text: 'заряд, держащийся только в эл цепи', stat: false}, {text: 'заряд, протекающий сквозь структуру атомов', stat: false}, {text: 'заряд, который  надо поддерживать, иначе он мгновенно угаснет', stat: false}],
        [{text: '10м', stat: true}, {text: '7,6м ', stat: false}, {text: '100м', stat: false}, {text: '1м', stat: false}],
        [{text: 'Это движущая сила для электрического заряда', stat: true}, {text: 'это поддерживающая сила для электрона', stat: false}, {text: 'это сила тока', stat: false}],
        [{text: 'сгорит и нарушится индуктивность', stat: true}, {text: '', stat: false}, {text: '', stat: false}]
    ]
];

//массив с информацией
let information = [
  [' На весах стоит параллелепипед 2*3*1 см. Его масса - 13,8 г. Из какого материала он состоит', 'Какой вес латунного шарика обьемом 15 см3', 'Высота столба нефти 50м, какое давление возникает на глубине 4м(плотность считать 900кг/м3)', 'Формула давления в РТУТИ', 'У меня есть 4 подвижных блока и F = 500H, какой вес я могу поднять', 'Масса человека, стоящего на лыжах и лыж вместе - 56кг. Длина лыж - 1,5м, ширина - 5 см. Какое давление оказывается на снег', 'Из какого материала изготовлена полка 2*10*30см массой 240 г(ответ приближенный)', 'В кастрюлю налили 25 см пресной воды. Какое давление на дне кастрюли, если ее диаметр 27.5см', 'Трактор работал пол дня и проделал путь 48км, его сила 900Ню Какую энергию он затратил', 'Могу ли я уравнять рычаг, если я стою на конце 5 м рычага и вешу n кг, а на другом плече длиной 100м  лежит кубик 0.01 n']
  , 
  ['Для чего нужны шипы на шинах', 'Давление на дно керосина 860кПа, какая высота столба керосина', 'Один и тот же объект поставили сначала на доску с длиной 30см и шириной 20см, а потом на 5 цилиндров, каждый из которых имеет площадь 120 см². Насколько различается давление, оказываемое ими на поверхность, если масса цилиндров и полки равна', 'У вентилятора мощность 400Вт, работал он 40 секунд. Какую работу он совершил','Как измениться жесткость пружины, если в центре сделать засечку','Я стою на самом углу рычага, допустим мой вес 45 кг, какой максимальный вес я могу поднять, стоящий на длинном конце рычага длинной в 5 м, соотношение рычагов 4:5','Что такое водоизмещение','Судно по­гру­же­но в прес­ную воду до ва­тер­ли­нии и при этом вы­тес­ня­ет воду объ­е­мом 15000 ку­би­че­ских мет­ров. Из­вест­но, что вес са­мо­го судна со­став­ля­ет 5*107 Нью­тон. Чему равна грузоподъёмность','В проводе напряжение 150В, это происходило 15 сек, какова сила тока', 'Продолжите цепочку : I->B-> ...->ЭДС (В)']
,
['От чего зависит индуктивность, дополните:  от кол-ва витков;S сечения;магнитной проничаемости и...','Где можно встретить 60гц','Странный кварк это','Na + Cl2 =','Ф=LI','W=qEd','Несамостоятельный заряд это','Сколько должна быть высота водяной трубки,чтобы она уравновешивалась с  давлением воздуха','Что такое напряжение','Что будет, если неправильно припаять медную катушку']
]


//перебор массива ответов для создания текста 'верно' и 'неверно'
for (let i = 0; i < data.length; i++) {
    for (let j = 0; j < data[i].length; j++) {
        for (let k = 0; k < data[i][j].length; k++) {
            if (data[i][j][k].stat === true) {
                data[i][j][k].res = 'верно'
            } else if (data[i][j][k].stat === false) {
                data[i][j][k].res = 'неверно'
            }
        }
    }
}

import testComponent from './components/test.vue'

export default {
  name: 'App',
  components: {
    testComponent,
  },
  data() {
    return {
            counter: 0,
            trueCounter: 0,
            shown: [],
            isTask: false,
            result: undefined,
            comment: undefined, 
            difficulty: 0
    }
  },
  computed: {
    tasks() {
      return data[this.difficulty][this.counter]
    },
    taskInfo() {
       return information[this.difficulty][this.counter]
    }
  },
  methods: {
            respond(arg) { // отвечает на событие дочернего компонента
            this.shown[this.counter] = arg.res
            this.trueCounter += arg.stat
            this.counter++;
        },
        reDo() { // переделать тест
            this.counter = 0
            this.trueCounter = 0
            this.shown = []
        }, 
        reset() { // ссылка на главную страницу
            this.reDo()
            this.isTask = false;
            this.difficulty = undefined;
        },
        start(diff) { // начать тест
            this.isTask = true;
            this.difficulty = diff
        },
        getRes() { //вычисляет результат в процентах
            return Math.round((this.trueCounter / 10) * 100) 
        }
  },
  watch: { //наблюдатели
        counter() { //за счетчиком
            if (this.counter == 10) {
                if (this.trueCounter == 10) {
                    this.result = 'Молодец, все верно.'
                    this.comment = 'Продолжай в том же духе! Можешь пройти тест посложнее.'
                } else if (this.trueCounter == 9) {
                    this.result = `Ты набрал ${this.getRes()}%`
                    this.comment = 'Всего одна ошибка. Просто исправь ее и все.'
                } else if (this.trueCounter < 9 && this.trueCounter >= 6) {
                    this.result = `Ты набрал ${this.getRes()}%`
                    this.comment = 'Это четверка. Повтори некоторые темы.'
                } else if (this.trueCounter >= 3 && this.trueCounter < 6) {
                    this.result = `Ты набрал ${this.getRes()}%`
                    this.comment = 'Это на тройку. Серьезно повтори все темы.'
                } else if (this.trueCounter < 3) {
                    this.result = 'Ты набрал меньше 30%'
                    this.comment = 'Это очень мало. Возможно, физика не для тебя.'
                }
            } 
        }
    },
}
</script>

