<template>
  <div id="test-container">
        <ol id="task">
        <li v-for="(task, index) in tasks" class="list-tasks" :key="index">
            <button @click="sendReq(task)">{{task.text}}</button>
        </li>
    </ol>
  </div>
</template>

<script>
export default {
  name: 'testComponent',
  props: ['tasks'],
  methods: {
            sendReq(req) { //отправляет родителю событие с параметром
            this.$emit('req-sub', req)
        }
  },
  emits: ['req-sub'],
}
</script>

